@use "../../foundation/mixins" as *;
@use "../../foundation/variables" as *;

.p-concept {
  position: relative;
  padding: 6.6rem 0 5.5rem 0;
  // 全人類、 ミュージシャン計画。
  &__title {
    h2 {
      font-size: 3.5rem;
      text-align: center;
      line-height: calc(50 / 35);
    }
    p {
      font-size: 1.8rem;
      margin-top: 1.7rem;
      margin-bottom: 3.4rem;
      text-align: center;
      line-height: calc(30 / 18);
    }
  }

  // 親要素
  &__flow {
    position: relative;
    // 子要素（英語・●・日本語）
    &-detail {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  // 英語部分
  &-flow-detail__en {
    font-size: 2.5rem;
    line-height: calc(50 / 25);
    text-align: right;
    width: 18rem;
  }

  // 矢印全体
  &-flow-detail__circle {
    display: flex;
    align-items: center;
    flex-direction: column;
    // ●アイコンサイズ
    background-color: $color-red;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    margin: 0 3rem;

    // 矢印の上部（屋根部分）
    // 最初の子要素にbefore擬似要素
    .p-concept__flow-detail:nth-of-type(1) &::before {
      top: -55%;
      left: 50%;
      content: "";
      position: absolute;
      transform: rotate(45deg);
      width: 2.5rem;
      height: 2.5rem;
      border-top: 0.3rem solid rgb(203, 0, 0); //太さ3px
      border-left: 0.3rem solid rgb(203, 0, 0); //太さ3px
      transform-origin: left top;
    }

    // 矢印の縦線部分
    .p-concept__flow-detail:nth-of-type(1) &::after {
      content: "";
      position: absolute;
      top: -2.5rem;
      transform: translateX(-50%);
      width: 0.3rem; //太さ3px
      height: 15.8rem;
      background-color: $color-red;
      transform-origin: left top;
      left: 44rem;
    }
  }

  // 説明部分
  &-flow-detail__ja {
    font-size: 2.3rem;
    line-height: calc(50 / 23);
    width: 18rem;
  }
}
